@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alfa+Slab+One&display=swap);
* {
  box-sizing: border-box !important;
}

html {
  font-family: "Roboto", sans-serif;
}

body {
  margin: 0;
  background: rgb(227, 23, 116);
  font-size: 14px !important;
  background: linear-gradient(
    90deg,
    rgba(227, 23, 116, 0.9192051820728291) 0%,
    rgba(5, 158, 150, 0.802) 100%
  );
  color: rgba(0,0,0,.85);
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,Noto Sans,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol,Noto Color Emoji;
  font-variant: tabular-nums;
  line-height: 1.5715;
  background-color: #fff;
  -webkit-font-feature-settings: "tnum","tnum", "tnum";
          font-feature-settings: "tnum","tnum", "tnum";
}

.slide-in-left-enter {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.slide-in-left-enter-active {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
  transition: all 200ms;
}

.slide-in-left-exit {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
  opacity: 1;
}

.slide-in-left-exit-active {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  opacity: 0;
  transition: all 200ms;
}

.center {
  text-align: center !important;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
}


.main-header {
  width: 100%;
  height: 5rem !important;
  display: flex !important;
  align-items: center !important;
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  background: #ffffff !important;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26) !important;
  padding: 0 1rem !important;
  z-index: 5 !important;
}

main {
  margin-top: 9rem !important;
}

@media (min-width: 768px) {
  .main-header {
    justify-content: space-between;
  }
}

.nav-links {
  list-style: none;
  font-family: "Roboto", sans-serif !important;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center !important;
  align-items: center !important;
}

.nav-links li {
  margin: 1rem !important;
  font-family: "Roboto", sans-serif !important;
}

.nav-links a {
  border: 1px solid transparent;
  font-family: "Roboto", sans-serif !important;
  color: #292929;
  text-decoration: none;
  padding: 0.5rem;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
  background: #f3f3f3;
  border-radius: 10px;
  /* padding: 0.9rem; */
  color: #292929;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.nav-links button {
  cursor: pointer;
  border: transparent;
  color: #292929;
  background: transparent;
  padding: 0.5rem;
  font: inherit;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover,
.nav-links button:active {
  background: #292929;
  color: white;
}

@media (max-width: 1696px) {
  .nav-links a:hover,
  .nav-links a:active,
  .nav-links a.active {
    background: transparent;
    border-radius: 10px;
    color: #292929;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
  }
  .nav-links button:hover,
  .nav-links button:active {
    background: transparent;
    color: white;
  }
}

@media (min-width: 1392px) {
  /*768px min width*/
  .nav-links {
    flex-direction: row;
  }

  .nav-links li {
    margin: 0 0.5rem !important;
  }

  .nav-links a {
    color: rgb(0, 0, 0) !important;
    text-decoration: none !important;
  }

  .nav-links button {
    color: rgb(0, 0, 0);
    background: transparent;
  }

  .nav-links button:hover,
  .nav-links button:active {
    background: #f3f3f3;
    border-radius: 10px;
    /* padding: 0.9rem; */
    color: #292929;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
  }
}

.side-drawer {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  height: 100vh;
  width: 58%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}

@media (max-width: 1392px) {
  .side-drawer {
    width: 28%;
  }
}

@media (max-width: 900px) {
  .side-drawer {
    width: 30%;
  }
}

@media (max-width: 690px) {
  .side-drawer {
    width: 40%;
  }
}
@media (max-width: 521px) {
  .side-drawer {
    width: 50%;
  }
}

@media (max-width: 403px) {
  .side-drawer {
    width: 60%;
  }
}

@media (max-width: 322px) {
  .side-drawer {
    width: 70%;
  }
}

@media (min-width: 1392px) {
  .side-drawer {
    display: none;
  }
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
}


.main-navigation__menu-btn {
  width: 3rem;
  height: 3rem;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  margin-right: 2rem;
  cursor: pointer;
}

.main-navigation__menu-btn span {
  display: block;
  width: 3rem;
  height: 0.4rem;
  background: rgb(204, 204, 204);
  /* background: rgb(231, 44, 220);
  background: linear-gradient(
    90deg,
    rgba(231, 44, 220, 0.9192051820728291) 0%,
    rgba(31, 224, 200, 0.76234243697479) 100%
  ); */
}

.main-navigation__title {
  color: rgb(255, 255, 255);
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  
}

.main-navigation__title a {
  text-decoration: none !important;
  border-radius: 20px !important;
  padding: 1rem !important;
  background: linear-gradient(to right, #c712b8 0%, #54a4ee 100%) !important;
  background-clip: text !important;
  -webkit-background-clip: text !important;
  -webkit-text-fill-color: transparent !important;
  font-family: "Alfa Slab One", cursive !important;
  font-style: italic !important;
  -webkit-border-radius: 20px !important;
  -moz-border-radius: 20px !important;
  -ms-border-radius: 20px !important;
  -o-border-radius: 20px !important;
  font-size: 1.17em !important;
  font-weight: bolder !important;
}

.main-navigation__title {
  margin-top: 1rem !important;
  margin-bottom: 1rem !important;
}

.main-navigation__header-nav {
  display: none;
}

.main-navigation__drawer-nav {
  height: 100%;
}

@media (min-width: 1392px) {
  /*768px min width*/
  .main-navigation__menu-btn {
    display: none;
  }

  .main-navigation__header-nav {
    display: block;
  }
}

.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}

.lds-dual-ring:after {
  content: ' ';
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #510077;
  border-color: #510077 transparent #510077 transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}

.loading-spinner__overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

